<template>
  <div class="pop-container">
    <div :class="{
        'resource-manager': true,
        'resource-manager-hide': !showResource
      }">
      <div class="resource-list" v-show="showResource">
        <div
          :class="{
            resource: true,
            'current-menu': currentMenuList[activePage] === title
          }"
          v-for="(item, title) of componentInfo"
          @click.stop="showMenu(title, { auto: false })"
          :key="title">
          {{ item.title }}
        </div>
      </div>
      <div class="resource-show" @click.stop="switchShowResource">
        <Icon
          class="show-icon"
          type="ios-arrow-back"
          title="隐藏"
          v-if="showResource">
        </Icon>
        <Icon
          title="展开"
          type="ios-arrow-forward"
          class="show-icon"
          v-else>
        </Icon>
      </div>
    </div>
    <div
      :class="{
      'content-board': true,
      'content-board-resource-show': showResource
      }">
      <template
        v-for="(page, index) of pageList"
        v-if="page.length">
        <div
          :class="pageNum === 2 ? 'double-page' : 'single-page'"
          @click="changeActivePage(index)"
          :key="index">
          <div class="tabs" v-if="currentMenuList[index]" @drop="handleDrop(index)" @dragover="handleDragOver">
            <div
              v-for="(title, idx) of pageList[index]"
              :class="{
                'tab-item': true,
                'current-menu': index - activePage === 0 && currentMenuList[index] === title
              }"
              @click="showMenu(title, { auto: false, page: index })"
              draggable
              @dragstart="handleDragStart(index, title)"
              @dragover="handleDragOverTab(index, title)"
              @dragleave="handleDragLeave"
              @dragend="handleDragEnd"
              :key="idx">
              <span class="tab-content">
              {{ idx + 1 }} {{ componentInfo[title].title }}
              </span>
              <Icon
                class="close"
                type="ios-close"
                @click.stop="hideMenu(title, index)"
                title="关闭">
              </Icon>
            </div>
            <div class="placeholder"></div>
            <div
              class="refresh"
              title="刷新内容"
              @click.stop="refreshContent(index)">
              <Icon type="md-refresh-circle" />
            </div>
            <div
              v-if="pageNum === 1 && (pageList[activePage].length > 1)"
              @click.stop="splitPage()"
              title="向右分页Ctrl+Shift+S"
              class="devide-page-button">
              <div class="devide-page-box"></div>
              <div class="devide-page-box"></div>
            </div>
            <div
              title="关闭当前分页"
              class="close-page"
              @click.stop="closePage(index)"
              v-if="pageNum > 1">
              <Icon class="close-icon" type="ios-close"></Icon>
            </div>
          </div>
          <div
            class="contents"
            v-if="currentMenuList[index] && hideContent !== index">
            <component
              class="componet"
              v-bind="componentInfo[currentMenuList[index]].props"
              :is="componentInfo[currentMenuList[index]].component">
            </component>
          </div>
        </div>
        <!-- <devide v-if="pageNum === 2 && index === 0" :key="index+'-devide'"/> -->
      </template>
    </div>
  </div>
</template>

<script>
import componentsList from '@/components/pop-menus'
import networkDisk from '@/components/network-disk'
// import devide from '@/components/devide'

let data = {
  title: '网盘',
  component: networkDisk,
  original: true,
  props: {}
}
Object.assign(componentsList, {
  networkDisk: data
})

export default {
  // components: {
  //   devide
  // },
  data () {
    return {
      showResource: true,
      currentMenu: '',
      lockCurrentMenu: false,
      /**
       * 常驻组件：不能注销，不会被反复注册
       * 普通组件：可以注销，重复注册会被顶替
       */
      componentInfo: {
        ...componentsList
      },
      conflictInfo: [
        ['codeTest', 'nodeTree']
      ],
      pageList: [[], []],
      currentMenuList: ['', ''],
      activePage: 0,
      showList: [],
      dragInfo: {},
      hideContent: -1
    }
  },
  computed: {
    pageNum () {
      let pages = 0
      for (let page of this.pageList) {
        if (page.length) {
          pages++
        }
      }
      return pages
    }
  },
  created () {
    this.$bus.$on('pop-helper-show-menu', this.showMenu)
    this.$bus.$on('pop-helper-hide-menu', this.hideMenu)
    this.$bus.$on('pop-helper-update-prop', this.updateProps)
    this.$bus.$on('pop-helper-register-component', this.registerComponent)
  },
  mounted () {},
  beforeDestroy () {
    this.$bus.$off('pop-helper-show-menu', this.showMenu)
    this.$bus.$off('pop-helper-hide-menu', this.hideMenu)
    this.$bus.$off('pop-helper-update-prop', this.updateProps)
    this.$bus.$off('pop-helper-register-component', this.registerComponent)
  },
  methods: {
    registerComponent (type, options) {
      if (this.componentInfo[type] && this.componentInfo[type].original) return
      let data = {
        title: options.title || '',
        component: options.component,
        props: options.props || {}
      }
      this.componentInfo[type] = data
    },
    unRegisterComponet (type) {
      if (!this.componentInfo[type]) return
      if (this.componentInfo[type].original) return
      this.hidePagesMenu(type)
      delete this.componentInfo[type]
    },
    updateProps (type, props) {
      if (this.componentInfo[type]) {
        let data = this.componentInfo[type].props
        this.componentInfo[type].props = Object.assign({}, data, props)
      }
      // for (let idx in this.currentMenuList) {
      //   if (type === this.currentMenuList[idx]) {
      //     this.refreshContent(idx)
      //   }
      // }
    },
    showMenu (type, options = {}) {
      let page = this.activePage
      if (options.hasOwnProperty('page')) {
        page = options.page
      }
      if (!options.hasOwnProperty('drag')) {
        for (let idx in this.pageList) {
          if (this.pageList[idx].includes(type)) {
            page = idx
            break
          } else if (this.conflictShow(idx, type)) {
            page = idx ^ 1
          }
        }
      }
      if (page >= this.pageList.length) {
        if (this.pageList.length >= 2) {
          page = 0
        } else {
          this.pageList.push([])
        }
      }
      if (!this.componentInfo[type]) return
      let position = options.hasOwnProperty('position') ? options.position : null
      if (!this.pageList[page].includes(type)) {
        if (position !== null) {
          this.pageList[page].splice(position, 0, type)
        } else {
          this.pageList[page].push(type)
        }
      } else {
        let oldIndex = this.pageList[page].indexOf(type)
        if (position && position !== oldIndex) {
          if (oldIndex < position) {
            position -= 1
          }
          this.pageList[page].splice(oldIndex, 1)
          this.pageList[page].splice(position, 0, type)
        }
      }
      this.changeActivePage(page)
      this.currentMenuList.splice(page, 1, type)
    },
    hideMenu (type, idx = null) {
      if (idx === null) {
        idx = this.activePage
      }
      let index = this.pageList[idx].indexOf(type)
      if (index !== -1) {
        this.pageList[idx].splice(index, 1)
      }
      if (this.currentMenuList[idx] === type) { // 如果是当前选择的菜单
        this.currentMenuList[idx] = ''
        let nextShow = (index !== -1 && this.pageList[idx].length > index) ? index : index - 1
        if (nextShow !== -1) {
          let showType = this.pageList[idx][nextShow]
          this.showMenu(showType, { auto: false })
        } else {
          this.closePage(idx)
        }
      }
    },
    conflictShow (idx, menu) {
      for (let list of this.conflictInfo) {
        if (!list.includes(menu)) continue
        for (let item of list) {
          if (menu !== item && this.pageList[idx].includes(item)) {
            return true
          }
        }
      }
      return false
    },
    hidePagesMenu (type) {
      for (let index = 0; index < this.pageList.length; index++) {
        this.hideMenu(type, index)
      }
    },
    refreshContent (idx) {
      this.hideContent = Number(idx)
      setTimeout(() => {
        this.hideContent = -1
      }, 100)
    },
    switchShow () {
      this.show = !this.show
    },
    splitPage () {
      let type = this.currentMenuList[this.activePage]
      let idx = this.pageList[0].length ? 0 : 1
      this.hideMenu(type, idx)
      if (!this.pageList[0].length) {
        this.pageList.splice(0, 1)
        this.pageList.push([])
        this.currentMenuList[0] = this.currentMenuList[1]
        this.currentMenuList[1] = ''
      }
      this.showMenu(type, { page: 1 })
    },
    closePage (idx) {
      let list = this.pageList[idx]
      if (list) {
        list.splice(0, list.length)
        this.currentMenuList[idx] = ''
      }
      if (this.activePage === idx) {
        this.activePage = idx ^ 1
      }
    },
    changeActivePage (idx) {
      idx = Number(idx)
      this.activePage = idx
    },
    switchShowResource () {
      this.showResource = !this.showResource
    },
    handleDrop (index) {
      if (!this.dragInfo.dragging) return
      let drag = this.dragInfo.drag
      let drop = this.dragInfo.onTab
      this.dragInfo = {}
      if (drag.index === index && drop && drop.title === drag.title) return
      if (drop && !this.pageList[index].includes(drop.title)) return
      let showOptions = {
        page: index,
        drag: true
      }
      if (drop) {
        let dropIndex = this.pageList[index].indexOf(drop.title)
        if (drop.position === 'after') {
          dropIndex += 1
        }
        showOptions.position = dropIndex
      }
      if (index !== drag.index) {
        this.hideMenu(drag.title, drag.index)
      }
      this.showMenu(drag.title, showOptions)
    },
    handleDragOver (event) {
      if (this.dragInfo.dragging) {
        event.preventDefault()
      }
    },
    handleDragStart (index, title) {
      this.dragInfo.dragging = true
      this.dragInfo.drag = {
        index,
        title
      }
    },
    handleDragOverTab (index, title) {
      this.dragInfo.onTab = {
        title,
        index
      }
      let rect = event.target.getBoundingClientRect()
      if (event.clientX > rect.left + rect.width / 2) {
        this.dragInfo.onTab.position = 'after'
      } else {
        this.dragInfo.onTab.position = 'before'
      }
    },
    handleDragLeave () {
      delete this.dragInfo.onTab
    },
    handleDragEnd () {
      this.dragInfo = {}
    }
  }
}
</script>

